.row {    color: lightgray;    margin-left: 20px;}/* row posters is the container, if you need a quick css review look at 1:13:00 in netflix clone */.row__posters {    display: flex;    overflow-y: hidden;    overflow-x: scroll;    padding: 20px;}    /* hide scroll bar */    .row__posters::-webkit-scrollbar {        display: none;    }.row__poster {    object-fit: contain;    width: 100%;    max-height: 100px;    margin-right: 10px;    transition: transform 450ms;}.row__posterLarge {    max-height: 250px;}.row__poster:hover {    transform: scale(1.08);}.row__posterLarge:hover {    transform: scale(1.09);}.display {    width: 100%;    max-height: 400px;    min-height: 400px;}